body {
  font-family: 'TheJamsil';
  font-weight: bolder;
  }

header {
  height: 700px;
  text-align: left;
  font-size: 300px;
  margin-bottom: 50px;
  margin-left: 80px;
}
#index{
  font-size: 60px;
  
}
.container {
  display: grid;
  grid-template-rows: 400px 1fr;
  text-align: center;
  margin: 50px;
}

nav {
  font-family: 'TheJamsil';
  margin-left: 20px;
  font-size: 45px;
  margin-top: 150px;
  
}
nav > a {
  float: right;
}

#main1 {
  background-image: url('C:\Users\박나영\Desktop\sample\images\coding.jpg');
  height: 1500px;
  width: 70%;
  box-sizing: border-box;

}

#main2 {
  background-image: url('C:\Users\박나영\Desktop\sample\images\coding2.png');
  background-size: cover; 
  background-repeat: no-repeat; 
  background-position: center; 
  height: 1200px; 
  width: 30%;
  box-sizing: border-box;
  margin-top: 200px;
}

a:link, a:visited {
  background-color:#ffffff; 
  text-decoration: none;
  display: inline-block;
  font-size: 1.5em;
  margin-left: 200px;
}

button {
  font-family: 'TheJamsil';
  background-color: rgb(172, 172, 197);
  color: white ;
  padding: 10px 20px;
  margin: 20px; 
  font-size: 3em;
  cursor: pointer;
  transition-duration: 1s;
  float: left;
  margin-left: 50px;
  padding: 40px;
  border: none; /* 테두리 없앰 */
  border-radius: 5px; /* 버튼 모서리 둥글게 설정 */
}

button:hover {
  background-color: orange;
  color: white;
}
